import { useContext, useState, createContext } from 'react'
import { IAppState, IAlert, IAppContext } from './AppContext.types';

const AppContext = createContext<IAppState | null>(null)

const useAppState = () => {
  const [ alert, setAlert ] = useState<IAlert>({
    show: false,
    message: "",
    status: 'info'
  });

  const closeAlert = () => {
    setAlert({ ...alert, show: false, message: "" });
  };
  
  return { alert, setAlert, closeAlert }
}


export const AppProvider = ({ children }: IAppContext) => {
	const appData = useAppState()
	return <AppContext.Provider value={appData}>{children}</AppContext.Provider>
}

//use this hook to consume the context
export const useAppContext = () => {
	return useContext(AppContext)
}