import axios, { AxiosRequestConfig } from "axios"
import history from "../utils/History"

const api = axios.create({
	baseURL: process.env.REACT_APP_SERVER_URL,
	withCredentials: true
})

function filterPathsReq(config: AxiosRequestConfig) {
	//return false to stop.

	if (config.url) {
		// #TODO: Add signin route
		return ![
			"refresh-token",
			"email",
			"email-verifier",
			"login",
			"fresh-login"
		].includes(String(config.url))
	}
	return true
}

//interceptors
api.interceptors.request.use(
	function (config: AxiosRequestConfig) {
		//no headers exists already so
		// config.headers = config.headers ?? {}
		//or we can use bang ! non-nullable assertion operator here.
		config.headers!["Authorization"] = `Bearer ${localStorage.getItem(
			"access_token"
		)}`
		return config
	},
	function (error) {
		return Promise.reject(error)
	},
	{ runWhen: filterPathsReq }
)

api.interceptors.response.use(
	response => {
		const originalRequest = response.config
		const pathname = originalRequest?.url?.split("/")?.at(-1) || ""

		if(response?.data?.errors?.[0]?.message === "Token expired") {
			if (['graphql'].includes(pathname)) {
				refreshRequest(originalRequest)
			}
		}
		return response
	},
	function (error) {
		const originalRequest = error.config
		const pathname = originalRequest.url.split("/").at(-1)

		if (
			[401, 422].includes(error.response.status) &&
			["email-verifier", "login"].includes(pathname)
		) {
			return Promise.reject(error)
		}

		if (
			[401, 422].includes(error.response.status) &&
			["token", "refresh-token"].includes(pathname)
		) {
			//remove the tokens and then push back to sign in page.
			localStorage.removeItem("access_token")
			localStorage.removeItem("refresh_token")
			history.push("/sign-in")
			return Promise.reject(error)
		}

		if ([401].includes(error.response.status) && !originalRequest._retry) {
			originalRequest._retry = true
			refreshRequest(originalRequest)
		}

		return Promise.reject(error)
	}
)

function refreshRequest(originalRequest: any) {
	const refreshToken = localStorage.getItem("refresh_token")
		return api
			.post("refresh-token", null, {
				headers: {
					Authorization: "Bearer " + refreshToken
				}
			})
			.then(res => {
				if (res.status === 200) {
					localStorage.setItem("access_token", res.data.access_token)
					originalRequest.headers["Authorization"] =
						"Bearer " + res.data.access_token
					return axios(originalRequest)
				}
			})
			.catch(err => {
				return Promise.reject(err)
			})
}

export { api }
