import { api } from "./config"

export const get = (url: string, options = {}) => {
	return api.get(url, options)
}

export const post = <T>(url: string, data = {}, options = {}): Promise<T> => {
	return api.post(url, data, {
		...options
	})
}

export const put = <T>(url: string, data = {}, options = {}): Promise<T> => {
	return api.put(url, data, { ...options })
}

export const remove = <T>(url: string, data = {}, options = {}): Promise<T> => {
	return api.delete(url, { data, ...options })
}
