import { useCallback, useState } from "react"

export default function useLocalStorage(key: string, initialValue = "") {
	const [storedValue, setStoredValue] = useState(() => {
		try {
			//get the token from localStorage
			const token = localStorage.getItem(key)
			//return token or initial value
			return token ? token : initialValue
		} catch (e) {
			return initialValue
		}
	})

	const setLocalStorage = useCallback((value: any) => {
		try {
			setStoredValue(value)
			//set to Local Storage
			const storageValue =
				typeof value === "object" ? JSON.stringify(value) : value
			localStorage.setItem(key, storageValue)
		} catch (e) {
			console.log("Error while setting token")
		}
	}, [])
	return { storedValue, setLocalStorage }
}
