import { createTheme, Color } from "@mui/material"
import { purple } from "@mui/material/colors"
import createPalette from "@mui/material/styles/createPalette"
// import {} from "@mui/styled-engine"
import { TrueBlue, GhostWhite, KellyGreen, RedSalsa, Grey } from "./colors"

declare module "@mui/material/styles" {
	interface TypeText {
		mute?: string
	}
	interface TypeBackground {
		backgroundGradientMulti?: (angle?: string) => string
		backgroundGradientSingle?: (angle?: string) => string
	}
}

type ColorPartial = Partial<Color>

declare module "@mui/material/styles/createPalette" {
	interface PaletteColor extends ColorPartial {}
}

const palette = createPalette({
	primary: purple,
	secondary: GhostWhite,
	error: RedSalsa,
	success: KellyGreen,
	grey: Grey,
	action: {
		active: "#969696"
	},
	text: {
		primary: "#414141",
		mute: Grey['500']
	},
	divider: Grey[200],
	background: {
		backgroundGradientMulti: (angle = "63deg") =>
			`linear-gradient(${angle}, rgba(252,252,255,1) 0%, rgba(253,254,255,1) 5%, rgba(245,252,254,1) 16%, rgba(244,249,253,1) 37%, rgba(250,249,255,1) 73%, rgba(242,249,252,1) 86%, rgba(255,255,255,1) 100%)`,
		backgroundGradientSingle: (angle = "90deg") =>
			`linear-gradient(${angle}, rgba(239,249,251,1) 0%, rgba(250,248,254,1) 100%)`
	}
})

export const theme = createTheme({
	palette,
	typography: {
		fontFamily: [
			"lato",
			"-apple-system",
			"BlinkMacSystemFont",
			"Segoe UI",
			"Roboto",
			"Helvetica Neue",
			"Arial",
			"sans-serif",
			"Apple Color Emoji",
			"Segoe UI Emoji",
			"Segoe UI Symbol"
		].join(",")
	},
	components: {
		MuiButton: {
			styleOverrides: {
				root: ({ ownerState, theme }) => ({
					textTransform: "none",
					letterSpacing: "0.55px",
					boxShadow: "none",
					fontWeight: "bold",
					borderRadius: theme.spacing(1),
					whiteSpace: "nowrap",
					"&:hover": {
						boxShadow: "none"
					}
				}),
				sizeMedium: ({ ownerState, theme }) => ({
					height: 38,
					padding: theme.spacing(1.3, 3)
				})
			}
		},
		MuiLink: {
			styleOverrides: {
				root: ({ ownerState, theme }) => ({
					"&:focus": {
						// textDecoration: 'underline',
						outline: "auto"
					}
				})
			}
		}
	}
})
